.logo {
  animation: bounce-in-fwd 4s;
}

@keyframes bounce-in-fwd {
  0% {
    opacity: 0;
    animation-timing-function: ease-in;
    transform: scale(0);
  }

  38% {
    opacity: 1;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  55% {
    animation-timing-function: ease-in;
    transform: scale(.7);
  }

  72% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  81% {
    animation-timing-function: ease-in;
    transform: scale(.84);
  }

  89% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  95% {
    animation-timing-function: ease-in;
    transform: scale(.95);
  }

  100% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }
}

